import FocusTrap from 'focus-trap-react';
import clsx from 'clsx';
import React, {
    useCallback,
    useRef,
    useEffect,
    useState,
    forwardRef,
    useImperativeHandle
} from 'react';
import { returnFocusAfterClosing } from '@element/react-common';
import {
    SideSheetDefaultProps,
    SideSheetPropTypes
} from './generated/propTypes';

const SideSheet = forwardRef((props, externalRef) => {
    const {
        attachContainerClassesToBody,
        children,
        className,
        headlineText,
        onClose,
        onOpen,
        open,
        returnFocusSelector,
        variant,
        allowOutsideClick,
        ...customProps
    } = props;

    const sideSheetRef = useRef();

    useImperativeHandle(externalRef, () => sideSheetRef.current, []);

    // CSS `min-width` media-query breakpoint for small screens
    const cssMediaQuerySmallScreenSize = 960;

    // CSS transition-duration in ms
    const cssTransitionTimeToClose = 75;

    // used to control style during hiding transition
    const [isHiding, setIsHiding] = useState(false);

    // used to check if focus trap should be enabled
    const [shouldFocusTrap, setShouldFocusTrap] = useState(false);

    const [isOpen, setIsOpen] = useState(() => open);

    const classNames = clsx(
        'lmnt',
        className,
        'lmnt-side-sheet', // don't even use this for CSS hooks
        variant === 'modal' && 'lmnt-side-sheet--modal',
        variant === 'floating' && 'lmnt-side-sheet--floating',
        variant === 'elevated' && 'lmnt-side-sheet--elevated',
        variant === 'coplanar' && 'lmnt-side-sheet--coplanar',
        isHiding ? 'lmnt-side-sheet--is-hiding' : '',
        open ? 'lmnt-side-sheet--is-visible' : ''
    );

    const scrimClassNames = clsx(
        'lmnt-side-sheet-scrim',
        variant === 'modal' && 'lmnt-side-sheet-scrim--modal',
        open ? 'lmnt-side-sheet-scrim--is-visible' : ''
    );

    const addBodyClass = useCallback(() => {
        variant === 'modal' &&
            document.body.classList.add('lmnt-side-sheet-container--modal');
        open &&
            document.body.classList.add(
                'lmnt-side-sheet-container--sheet-is-visible'
            );
    }, [open, variant]);

    const removeBodyClass = useCallback(() => {
        document.body.classList.remove('lmnt-side-sheet-container--modal');
        document.body.classList.remove(
            'lmnt-side-sheet-container--sheet-is-visible'
        );
    }, []);

    const isScreenSmall = useCallback(
        () => document.body.offsetWidth < cssMediaQuerySmallScreenSize,
        []
    );

    const lmntSideSheetDismiss = useRef(null);
    const focusSideSheetDismiss = () => {
        lmntSideSheetDismiss.current && lmntSideSheetDismiss.current.focus();
    };

    const isSideSheetModalVariant = useCallback(
        () => variant === 'modal',
        [variant]
    );

    const isSmallOrModal = useCallback(
        () => isSideSheetModalVariant() || isScreenSmall(),
        [isSideSheetModalVariant, isScreenSmall]
    );

    // only enable focus trap if conditions are met
    const checkFocusTrapConditions = useCallback(() => {
        const conditions = open && isSmallOrModal();
        conditions && setShouldFocusTrap(true);
    }, [open, isSmallOrModal, setShouldFocusTrap]);

    const closeSideSheet = useCallback(() => {
        setIsOpen(false);
        setShouldFocusTrap(false);
        setIsHiding(true);
        attachContainerClassesToBody && removeBodyClass();
        returnFocusAfterClosing(returnFocusSelector);
        onClose?.();

        // do not put onClose in the dependency array, it will cause this callback to be recreated every render
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [
        attachContainerClassesToBody,
        setShouldFocusTrap,
        setIsHiding,
        removeBodyClass,
        returnFocusSelector
    ]);

    const openSideSheet = () => {
        setIsOpen(true);
        checkFocusTrapConditions();
        attachContainerClassesToBody && addBodyClass();
        focusSideSheetDismiss();
        onOpen?.();
    };

    useEffect(() => {
        if (open) openSideSheet();
        else if (isOpen) closeSideSheet();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [open]);

    useEffect(() => {
        isHiding &&
            setTimeout(() => {
                setIsHiding(false);
            }, cssTransitionTimeToClose);
    }, [isHiding]);

    return (
        <>
            <FocusTrap
                active={shouldFocusTrap}
                focusTrapOptions={{
                    allowOutsideClick: true,
                    clickOutsideDeactivates: false,
                    returnFocusOnDeactivate: false
                }}
            >
                <aside
                    aria-hidden={!open}
                    aria-labelledby='lmnt-side-sheet__headline'
                    aria-modal='true'
                    className={classNames}
                    role='dialog'
                    {...customProps}
                >
                    <div className='lmnt-side-sheet__content'>
                        <header className='lmnt-side-sheet__header'>
                            <button
                                aria-label='Dismiss'
                                // TODO refactor so this is not necessary
                                // eslint-disable-next-line jsx-a11y/no-autofocus
                                autoFocus
                                className='mdc-icon-button mdc-button--text lmnt-side-sheet__dismiss'
                                onClick={closeSideSheet}
                                ref={lmntSideSheetDismiss}
                                type='button'
                            >
                                <i
                                    className='material-icons'
                                    aria-hidden='true'
                                >
                                    close
                                </i>
                            </button>
                            {headlineText && (
                                <span
                                    className='lmnt-side-sheet__headline mdc-typography--headline6'
                                    id='lmnt-side-sheet__headline'
                                >
                                    {headlineText}
                                </span>
                            )}
                        </header>
                        <div className='lmnt-side-sheet__body'>{children}</div>
                    </div>
                </aside>
            </FocusTrap>
            <div
                className={scrimClassNames}
                onMouseDown={allowOutsideClick && closeSideSheet}
                aria-hidden='true'
            />
        </>
    );
});

SideSheet.displayName = 'SideSheet';
SideSheet.propTypes = SideSheetPropTypes;
SideSheet.defaultProps = SideSheetDefaultProps;

export default SideSheet;
