import clsx from 'clsx';
import PropTypes from 'prop-types';
import React, { forwardRef } from 'react';

const SideSheetWrapper = forwardRef((props, externalRef) => {
    const { children, className, variant, ...customProps } = props;

    const classNames = clsx(
        'lmnt',
        className,
        'lmnt-side-sheet-wrapper', // don't even use this for CSS hooks
        variant === 'elevated' && 'lmnt-side-sheet-wrapper--elevated',
        variant === 'coplanar' && 'lmnt-side-sheet-wrapper--coplanar'
    );

    return (
        <div ref={externalRef} className={classNames} {...customProps}>
            {children}
        </div>
    );
});

SideSheetWrapper.displayName = 'SideSheetWrapper';
SideSheetWrapper.propTypes = {
    children: PropTypes.node,
    className: PropTypes.string,
    variant: PropTypes.oneOf(['coplanar', 'elevated'])
};

SideSheetWrapper.defaultProps = {
    children: null,
    className: null,
    variant: null
};

export default SideSheetWrapper;
