import clsx from 'clsx';
import PropTypes from 'prop-types';
import React, { forwardRef } from 'react';

const SideSheetContainer = forwardRef((props, externalRef) => {
    const { children, className, isSideSheetVisible, variant, ...customProps } =
        props;

    const classNames = clsx(
        'lmnt',
        className,
        'lmnt-side-sheet-container', // don't even use this for CSS hooks
        variant === 'modal' && 'lmnt-side-sheet-container--modal',
        isSideSheetVisible && 'lmnt-side-sheet-container--sheet-is-visible'
    );

    return (
        <div ref={externalRef} className={classNames} {...customProps}>
            {children}
        </div>
    );
});

SideSheetContainer.displayName = 'SideSheetContainer';
SideSheetContainer.propTypes = {
    children: PropTypes.node,
    className: PropTypes.string,
    isSideSheetVisible: PropTypes.bool,
    variant: PropTypes.oneOf(['modal'])
};

SideSheetContainer.defaultProps = {
    children: null,
    className: null,
    isSideSheetVisible: false,
    variant: null
};

export default SideSheetContainer;
